                  <span>Tugatish</span>
                </button>
                <button  class="next"  :class="{'actives':answers.length>activeIndex||test.choices.length==0||test.is_many_variants}"  @click="nextStart(test,i)">
                  <span>Keyngisi</span>
                </button>
              </div>
